import React, {useEffect, useState} from 'react'
import '../css/admin.css'
import {getCampaignContract, Phase, phaseName, prettyPrintBigNumber} from "../utils";
import {useWeb3React} from "@web3-react/core";
import {PhenomCampaign} from "../contracts/phenom-campaign";


export function AdminTable({updateHandler}) {
    const { active, account, library, connector, activate, setError, deactivate } = useWeb3React()
    // const [campaignContract, setCampaignContract] = useState<PhenomCampaign>()
    const [statistic, setStatistic] = useState({})
    const [withdrawing, setWithdrawing] = useState(false)

    useEffect(() => {(async () => {
        const contract = await getCampaignContract(library)
        const vestingStartTime = await contract.vestingStartTime()
        // await setCampaignContract(contract)

        for (let phaseIndex = Phase.SEED; phaseIndex < Phase.PUBLIC; phaseIndex++) {
            const phase = phaseName(phaseIndex)
            setStatistic(prevState => {
                return {
                    ...prevState,
                    [phase]: {
                        ...prevState[phase],
                        phaseIndex: phaseIndex
                    }
                }
            })
            contract.getReleasedAmountByPhase(phaseIndex).then((released) => {
                setStatistic(prevState => {
                    return {
                        ...prevState,
                        [phase]: {
                            ...prevState[phase],
                            released: prettyPrintBigNumber(released)
                        }
                    }
                })
                contract.getAvailableAmountByPhase(phaseIndex).then((available) => {
                    setStatistic(prevState => {
                        return {
                            ...prevState,
                            [phase]: {
                                ...prevState[phase],
                                unlocked: prettyPrintBigNumber(available)
                            }
                        }
                    })
                })
            })

            contract.getInvestedAmountByPhase(phaseIndex).then((invested) => {
                setStatistic(prevState => {
                    return {
                        ...prevState,
                        [phase]: {
                            ...prevState[phase],
                            invested: prettyPrintBigNumber(invested)
                        }
                    }
                })
            })

            contract.getWithdrawableAmountByPhase(phaseIndex).then( async (withdrawable) => {
                const origin_withdrawable = withdrawable
                if (vestingStartTime.toNumber() === 0) {
                    // vesting not started yet
                    const preTgeTotal = await contract.preTgePurchaseTotals(phaseIndex)
                    withdrawable = withdrawable.sub(preTgeTotal)
                }
                setStatistic(prevState => {
                    return {
                        ...prevState,
                        [phase]: {
                            ...prevState[phase],
                            withdrawable: prettyPrintBigNumber(withdrawable),
                        }
                    }
                })
            })

            contract.getSupplyByPhase(phaseIndex).then( async (supply) => {
                setStatistic(prevState => {
                    return {
                        ...prevState,
                        [phase]: {
                            ...prevState[phase],
                            supply: prettyPrintBigNumber(supply)
                        }
                    }
                })
            })
        }
    })().then()}, [withdrawing, updateHandler])

    const withdraw = async (phaseIndex) => {
        setWithdrawing(true)
        try {
            const contract = await getCampaignContract(library)
            const tx = await contract.withdraw(phaseIndex, await contract.getWithdrawableAmountByPhase(phaseIndex))
            await tx.wait()
        } catch (e) {
            console.log(e)
        }
        setWithdrawing(false)
    }

    return (
        <>
            <div className={'ap-table'}>
                <div className={'ap-table-titles'}>
                    <h6 className={'ap-table-title'}>supply</h6>
                    <h6 className={'ap-table-title'}>invested</h6>
                    <h6 className={'ap-table-title'}>unlocked</h6>
                    <h6 className={'ap-table-title'}>released</h6>
                    <h6 className={'ap-table-title'}>left</h6>
                </div>
                { Object.entries(statistic).map( ([key, data]) => (
                    <div key={key} className={'ap-table-line'}>
                        <h3 className={'ap-table-line-title ap-table-line-title-first'}>{key.substring(2)}</h3>
                        <p className={'ap-table-line-value'}>{data['supply']}</p>
                        <p className={'ap-table-line-value'}>{data['invested']}</p>
                        <p className={'ap-table-line-value'}>{data['unlocked']}</p>
                        <p className={'ap-table-line-value'}>{data['released']}</p>
                        <p className={'ap-table-line-value'}>{data['withdrawable']}</p>
                        { data['withdrawable'] == '0' || <a className={'ap-table-line-link'} onClick={() => withdraw(data['phaseIndex'])}>withdraw</a> }
                    </div>
                )) }

                {/*<div className={'ap-table-line'}>
                    <h3 className={'ap-table-line-title ap-table-line-title-second'}>private round</h3>
                    <p className={'ap-table-line-value'}>100000</p>
                    <p className={'ap-table-line-value'}>45678</p>
                    <p className={'ap-table-line-value'}>1234</p>
                    <p className={'ap-table-line-value'}>1234</p>
                    <p className={'ap-table-line-value'}>1234</p>
                    <a className={'ap-table-line-link'}>withdrow</a>
                </div>*/}
            </div>
            <div className={'ap-table-mobile'}>
                        <div className={'ap-tab-line-one'}>
                            <h3 className={'ap-tab-mob-line-one-title'}>seed round</h3>
                            <a onClick={() => {document.querySelector('.ap-tab-info-one').classList.toggle('active')}} className={'ap-tab-mob-line-one-link'}>information</a>
                            <a className={'ap-tab-mob-line-link'}>withdrow</a>
                        </div>
                        <div className={'ap-tab-info-one-container'}>
                            <div className={'ap-tab-info-one'}>
                                <div className={'ap-tab-info-container'}>
                                    <p className="ap-tab-info-text">Supply</p>
                                    <p className={'ap-tab-info-value'}>100000</p>
                                </div>
                                <div className={'ap-tab-info-container'}>
                                    <p className="ap-tab-info-text">Invested</p>
                                    <p className={'ap-tab-info-value'}>45678</p>
                                </div>
                                <div className={'ap-tab-info-container'}>
                                    <p className="ap-tab-info-text">Unlocked</p>
                                    <p className={'ap-tab-info-value'}>1234</p>
                                </div>
                                <div className={'ap-tab-info-container'}>
                                    <p className="ap-tab-info-text">Released</p>
                                    <p className={'ap-tab-info-value'}>1234</p>
                                </div>
                                <div className={'ap-tab-info-container'}>
                                    <p className="ap-tab-info-text">Left</p>
                                    <p className={'ap-tab-info-value'}>1234</p>
                                </div>
                            </div>
                        </div>
                        <div className={'ap-tab-line-one ap-tab-line-two'}>
                            <h3 className={'ap-tab-mob-line-one-title'}>private round</h3>
                            <a onClick={() => {document.querySelector('.ap-tab-info-two').classList.toggle('active')}} className={'ap-tab-mob-line-one-link ap-tab-mob-line-two-link'}>information</a>
                            <a className={'ap-tab-mob-line-link'}>withdrow</a>
                        </div>
                        <div className={'ap-tab-info-two-container'}>
                            <div className={'ap-tab-info-two'}>
                                <div className={'ap-tab-info-container'}>
                                    <p className="ap-tab-info-text">Supply</p>
                                    <p className={'ap-tab-info-value'}>100000</p>
                                </div>
                                <div className={'ap-tab-info-container'}>
                                    <p className="ap-tab-info-text">Invested</p>
                                    <p className={'ap-tab-info-value'}>45678</p>
                                </div>
                                <div className={'ap-tab-info-container'}>
                                    <p className="ap-tab-info-text">Unlocked</p>
                                    <p className={'ap-tab-info-value'}>1234</p>
                                </div>
                                <div className={'ap-tab-info-container'}>
                                    <p className="ap-tab-info-text">Released</p>
                                    <p className={'ap-tab-info-value'}>1234</p>
                                </div>
                                <div className={'ap-tab-info-container'}>
                                    <p className="ap-tab-info-text">Left</p>
                                    <p className={'ap-tab-info-value'}>1234</p>
                                </div>
                            </div>
                        </div>
                    </div>
        </>
    )
}














