export enum Phase {
    SEED,
    PRIVATE,
    COMMUNITY_SALE_1,
    COMMUNITY_SALE_2,
    COMMUNITY_SALE_3,


    FUNDING_TEAM,
    ADVISORS,
    MARKETING,
    DEVELOPMENT,

    PUBLIC
}

export function phaseName(phase: Phase) {
    switch (phase) {
        case Phase.SEED: return '1. Seed Round'
        case Phase.PRIVATE: return '2. Private Round'
        case Phase.COMMUNITY_SALE_1: return '3. Community Sale 1'
        case Phase.COMMUNITY_SALE_2: return '4. Community Sale 2'
        case Phase.COMMUNITY_SALE_3: return '5. Community Sale 3'
        case Phase.FUNDING_TEAM: return '6. Funding Team'
        case Phase.ADVISORS: return '7. Advisors'
        case Phase.MARKETING: return '8. Marketing'
        case Phase.DEVELOPMENT: return '9. Development'
        default: return phase.toString()
    }
}

export const goerliChainId = 5
export const bscChainId = 56
export const currentChainId = goerliChainId

const phetaTokenAddress = {
    [goerliChainId]: '0xC0fDF2b87745eC351AF60E08CdDc4B54F3436F74'
}
export const campaignContractAddress = {
    [goerliChainId]: '0x8B78f45aBc5994e2DCBb8e9Be3Fb22077b9Bc74b'
}
const busdContractAddress = {
    [goerliChainId]: '0x271fc01c0d11e71Dd9fB4346D1Db1B584e6bf268'
}

let _campaignContract: PhenomCampaign

export async function getCampaignContract(library: Web3Provider): Promise<PhenomCampaign> {
    if (!_campaignContract) {
        const contract = new Contract(
            campaignContractAddress[currentChainId],
            PhenomCampaignABI,
            library.getSigner()
        )
        await contract.deployed()
        _campaignContract = contract as unknown as PhenomCampaign
    }
    return _campaignContract
}

