export function prettyPrintBigNumber(bn: BigNumber) {
    if (bn.isZero()) {
        return '0'
    }
    // if (bn.lt('100000000000000000')) {
    //     return '<0.1'
    // }
    const withoutFraction = Math.round(bn.div('1000000000000000000').toNumber())
    const fraction = bn.sub(BigNumber.from(withoutFraction).mul('1000000000000000000'))
    const formatted = (fraction.isZero() ? ethers.utils.formatEther(bn) : withoutFraction.toString())
        .replace(/\B(?=(\d{3})+(?!\d))/g, " ")
        .replace("000 000.0", 'kk')
        .replace("000.0", 'k')
        .replace(RegExp(".0$"), '')

    if (fraction.isZero()) {
        return formatted
    } else {
        return `~${formatted}`
    }
}

export async function addPhetaToken(phetaTokenContract: ERC20, account: string) {
    const phetaBalance = await phetaTokenContract.balanceOf(account)

    if (phetaBalance.isZero()) {
        const ethereum = window['ethereum']
        try {
            const wasAdded = await ethereum.request({
                method: 'wallet_watchAsset',
                params: {
                    type: 'ERC20',
                    options: {
                        address: phetaTokenAddress[currentChainId],
                        symbol: "PHETA", // A ticker symbol or shorthand, up to 5 chars.
                        decimals: 18,
                        image: 'https://user-images.githubusercontent.com/1183619/167500553-fd199209-cf7c-44a2-80fd-96f7d0ed247c.png' // A string url of the token logo
                    }
                }
            })

            if (wasAdded) {
                console.log('PHETA Token added');
            } else {
                console.warn('PHETA Token not added');
            }
        } catch (error) {
            console.error(error);
        }
    }
}
