import React, {useEffect} from "react";
import {Preloader} from "./pages/start-page";
import {InvestorPage} from "./pages/investor";
import {AdminPage} from "./pages/admin";
import {WalletConnect} from "./pages/start-page";
import {getCampaignContract} from "./utils";
import {useWeb3React} from "@web3-react/core";

export const App: React.FC = () => {
    const [language, setLanguage] = React.useState('en')//navigator.language)

    const [walletConnecting, setWalletConnecting] = React.useState(false)
    const [admin, setAdmin] = React.useState<boolean>()
    const { active, account, library, connector, activate, setError, deactivate } = useWeb3React()

    useEffect(() => {
        // setWalletConnecting(true)
        //     setWalletConnecting(false)
        if (account) {
            getCampaignContract(library).then(async (contract) => {
                const adminAddress = await contract.owner()
                setAdmin(adminAddress === account)
            })
        }
    }, [account])

    return (
        <>
            { walletConnecting && (
                <div>
                    <Preloader language={language}/>
                </div>
            )}
            { !account && !walletConnecting && (
                <div>
                    <WalletConnect language={language}/>
                </div>
            )}
            { account && admin === true && (
                <div>
                    <AdminPage/>
                </div>
            )}
            { account && admin === false && (
                <div>
                    <InvestorPage walletAddress={account} language={language}/>
                </div>
            )}
        </>
    )
}
